import React, { Component } from 'react'
import './Timer.css';

class Timer extends Component {
    constructor(props) {
        super(props);
        this.state = { days: 8, hours: 4, minutes: 15, seconds: 60, counting: true };
        this.startTimer = this.startTimer.bind(this);
        this.countdown = this.countdown.bind(this);
        this.timer = 0;
    }
    padNum(n) {
        if (n < 10) {
            return `0${n}`
        } else {
            return n
        }
    }
    countdown() {
        let seconds = this.state.seconds - 1;
        this.setState({ seconds: seconds });

        if (seconds === 0) {
            if (this.state.minutes > 0) {
                this.setState({ minutes: this.state.minutes - 1, seconds: 60 })
            } else if (this.state.hours > 0) {
                this.setState({ hours: this.state.hours - 1, minutes: 60, seconds: 60 })
            } else if (this.state.days > 0) {
                this.setState({ days: this.state.days - 1, hours: 24, minutes: 60, seconds: 60 })
            } else {
                this.setState({ counting: false })
            }
        }
        if (!this.state.counting) {
            clearInterval(this.timer);
        }

    }
    startTimer() {
        if (this.state.seconds > 0 && this.timer === 0) {
            this.timer = setInterval(this.countdown, 1200);
        }
    }
    componentWillUnmount() {
        clearInterval(this.timer)
    }
    render() {
        this.startTimer();
        return (
            <div className="main-container">
                <div className="card-container">
                    <div className="column-align">
                        <div className="num-card">
                            {this.padNum(this.state.days)}
                        </div>
                        <h2 className="label-text">Days</h2>
                    </div>
                    <div className="column-align">
                        <div className="num-card">
                            {this.padNum(this.state.hours)}
                        </div>
                        <h2 className="label-text">Hours</h2>
                    </div>
                    <div className="column-align">
                        <div className="num-card">
                            {this.padNum(this.state.minutes)}
                        </div>
                        <h2 className="label-text">Minutes</h2>
                    </div>
                    <div className="column-align">
                        <div className="num-card">
                            {this.padNum(this.state.seconds)}
                        </div>
                        <h2 className="label-text">Seconds</h2>
                    </div>
                </div>
            </div>
        )
    }
}

export default Timer;